.income__item {
  font-size: 1.4rem;
  padding: 1rem;
  border-top: 2px solid #e7e7e7;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.income__item:hover .income__icons {
  transform: translateX(-1rem);
}

.income__item:hover .income__icons .icon {
  transform: translateX(-1rem);
  opacity: 1;
}

.income__item:nth-child(even) {
  background-color: rgba(78, 122, 217, 0.1);
}

.income__item:last-child {
  border-bottom: 2px solid #e7e7e7;
}

.income__icons {
  color: #372fad;
  transition: all 0.3s;
  opacity: 1;
  display: flex;
  width: 15rem;
}

.income__icons .icon {
  opacity: 0;
}

.income__icons > * {
  margin: 0 0.5rem;
  cursor: pointer;
  transition: all 0.2s;
}

.income__icons > *:hover {
  color: #00035e;
}

.update__button--income {
  background-color: #372fad !important;
}
